.hobbies-album-button {
	text-align: center;
	font-size:20px;
}

.hobbies-album-button-container div{
	margin-top:10px;
	margin-bottom:10px;
	text-align: center;
}



@media only screen and (min-width:700px) {
    .hobbies-album-button-container {
        display:-ms-flexbox;
        display:flex;
        -ms-flex-wrap:wrap;
            flex-wrap:wrap;
        -ms-flex-pack: distribute;
            justify-content: space-around;
        margin-top:50px;
        margin-bottom:50px;
    }
}

@media only screen and (max-width:700px) {
    .hobbies-album-button-container {
        display:-ms-flexbox;
        display:flex;
        -ms-flex-wrap:wrap;
            flex-wrap:wrap;
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-pack: distribute;
            justify-content: space-around;
        margin-top:50px;
        margin-bottom:50px;
    }
}


